'use client'

import {Bot, PanelLeft} from "lucide-react";
import {useEffect, useRef, useState} from "react";
import clsx from "clsx";

const SidePanel = () => {
    const [isOpen, setIsOpen] = useState(true);
    const headerRef = useRef<HTMLDivElement | null>(null);
    const [isMouseOver, setIsMouseOver] = useState<boolean>(false);

    const togglePanel = () => {
        setIsOpen((prevState) => !prevState);
    }

    useEffect(() => {
        const element = headerRef.current as HTMLDivElement;

        const changeIconOver = () => {
            setIsMouseOver(true);
        }

        const changeIconOut = () => {
            setIsMouseOver(false);
        }

        if (element) {
            element.addEventListener('mouseover', changeIconOver);
            element.addEventListener('mouseout', changeIconOut);
        }

        return () => {
            if (element) {
                element.removeEventListener('mouseover', changeIconOver);
                element.removeEventListener('mouseout', changeIconOut);
            }
        }
    })

    const renderIcon = () => {
        if (isMouseOver) {
            return (<PanelLeft size={28} className="stroke-2 stroke-dark-text-primary cursor-pointer" />)
        } else {
            return (<Bot size={28} className="stroke-2 stroke-dark-text-primary"/>)
        }
    }

    return (
        <div ref={headerRef} className={clsx('h-full bg-dark-bg-primary transition', {'w-fit bg-dark-bg-secondary border-r-1 border-dark-border ': !isOpen, 'w-[15%] bg-dark-bg-primary': isOpen})}>
            <header className="w-full p-4 flex justify-between items-center">
                {
                    isOpen && <Bot size={28} className="stroke-2 stroke-dark-text-primary"/>
                }
                <button onClick={togglePanel}>
                    {
                        isOpen ? (
                            <PanelLeft size={28}
                                       className="stroke-2 stroke-dark-text-secondary cursor-pointer hover:stroke-dark-text-primary hover:scale-105 transition"/>

                        ) : (
                            renderIcon()
                        )
                    }
                </button>
            </header>
        </div>
    );
};

export default SidePanel;
